@import "config";
main {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-rows: 90vh 1fr;
  .nav--box {
    display: flex;
    width: 100vw;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    background-color: #000;
    &.hidden {
      display: none;
    }
    &-tag {
      font-family: "montserrat";
      font-size: 1rem;
      padding: 1rem;
      color: #fff;
      border: 0.1rem solid #fff;
      border-radius: 1rem;
      display: flex;
      align-items: center;
      text-align: center;
      &:active {
        color: #555;
        border-color: #555;
      }
    }
  }
  .list--container {
    @include centerGrid;
    .empty-tag {
      font-family: "montserrat";
      font-weight: 300;
      font-size: 1rem;
      font-style: italic;
    }
  }
}

.nav-container {
  @include centerGrid;
  width: 100vw;
  height: 100%;
  input {
    width: 100vw;
    height: 100%;
    padding: 1rem;
    font-size: 1.5rem;
    outline: none;
    border: none;
    text-align: center;
    font-family: "montserrat";
  }
}

.item {
  height: 4rem;
  width: 90vw;
  margin-bottom: 1rem;
  @include centerGrid;
  border: 0.2rem solid black;
  border-radius: 1rem;
  color: #000;
  &-complete {
    color: #00b00c;
    border-color: #00b00c;
  }
  h1 {
    font-family: "montserrat";
    font-weight: 600;
  }
}
