* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
}

main {
  grid-template-rows: 90vh 1fr;
  width: 100vw;
  height: 100vh;
  display: grid;
}

main .nav--box {
  background-color: #000;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 100%;
  display: flex;
}

main .nav--box.hidden {
  display: none;
}

main .nav--box-tag {
  color: #fff;
  text-align: center;
  border: .1rem solid #fff;
  border-radius: 1rem;
  align-items: center;
  padding: 1rem;
  font-family: montserrat;
  font-size: 1rem;
  display: flex;
}

main .nav--box-tag:active {
  color: #555;
  border-color: #555;
}

main .list--container {
  justify-content: center;
  align-items: center;
  display: grid;
}

main .list--container .empty-tag {
  font-family: montserrat;
  font-size: 1rem;
  font-style: italic;
  font-weight: 300;
}

.nav-container {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100%;
  display: grid;
}

.nav-container input {
  text-align: center;
  border: none;
  outline: none;
  width: 100vw;
  height: 100%;
  padding: 1rem;
  font-family: montserrat;
  font-size: 1.5rem;
}

.item {
  color: #000;
  border: .2rem solid #000;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  width: 90vw;
  height: 4rem;
  margin-bottom: 1rem;
  display: grid;
}

.item-complete {
  color: #00b00c;
  border-color: #00b00c;
}

.item h1 {
  font-family: montserrat;
  font-weight: 600;
}

